<template>
  <div class="flex-layout">
    <el-form :model="fromData" status-icon ref="fromData" label-width="100px" class="flex-ruleForm">
      <el-form-item>
        <div class="flex-img text-center" >
          <img src="./../assets/img/photo-default.png"/>
        </div>
      </el-form-item>
      <el-form-item prop="userName">
        <el-input type="text" placeholder="用户名" v-model="fromData.userName" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item prop="pass">
        <el-input type="password" placeholder="密 码" v-model="fromData.pass" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item prop="checkPass">
        <el-input type="password" placeholder="确认密码" v-model="fromData.checkPass" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item style="text-align: center">
        <el-button type="primary" @click="submitForm('fromData')">提交</el-button>
        <el-button @click="resetForm('fromData')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data () {
    return {
      fromData: {
        pass: '',
        userName: '',
        checkPass: ''
      }
    };
  },
  methods: {
    submitForm (formName) {
      let _this = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.axios.post('/user/login', this.fromData).then(function (response) {
            if (response) {
              if (response.data.data.length > 0) {
                let data = response.data.data[0];
                _this.$socket.emit('login', data);
                _this.$router.push({name: 'chatMain', params: {userId: data._id}})
              } else {
                _this.$message.error('登陆失败');
              }
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm (formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style scoped>
  .flex-ruleForm {
    width: 600px;
  }
</style>



